
@import "../packages/style/stylesheets/reset.css";
@import "../src/assets/font/iconfont.css";
.yun-h1{font-size:.36rem
}
.yun-h2{font-size:.34rem
}
.yun-h3{font-size:.32rem
}
.yun-h4{font-size:.28rem
}
.yun-h5{font-size:.26rem
}
.yun-h6{font-size:.22rem
}
.yun-small{font-size:.22rem
}
.yun-text-center{text-align:center
}
.yun-text-left{text-align:left
}
.yun-text-right{text-align:right
}
.yun-text-default{color:#030303
}
.yun-text-secondary{color:#98a1a8
}
.yun-text-theme,.yun-text-active{color:#3cbaff
}
.yun-text-warn{color:#f35959
}
.yun-text-tips{color:#7a797b
}
.yun-text-white{color:#ffffff
}
.yun-bg-blue{background-color:#3CBAFF
}
.yun-bg-olive{background-color:#94D301
}
.yun-bg-orange{background-color:#F87E4E
}
.yun-bg-yellow{background-color:#F7BF28
}
.yun-bg-purple{background-color:#AF65DF
}
.yun-bg-pink{background-color:#FB6691
}
.yun-bg-red{background-color:#F35959
}
.yun-bg-skyblue{background-color:#31D2EA
}
.yun-bg-royalblue{background-color:#4788D8
}
.yun-bg-green{background-color:#01D386
}
.yun-bg-brown{background-color:#C18E6F
}
.yun-bg-grey{background-color:#6D777D
}
.yun-bg-whitesmoke{background-color:#EAEFF3
}
.yun-bg-white{background-color:#FFFFFF
}
.yun-left{float:left
}
.yun-right{float:right
}
.yun-block-center{margin:0 auto
}
.yun-ellipse{display:block;overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
.yun-multi-ellipse{display:-webkit-box;overflow:hidden;text-overflow:ellipsis;-webkit-box-orient:vertical;-webkit-line-clamp:2
}
.yun-divided{position:relative
}
.yun-divided:after{content:'';display:block;height:.3rem;border-left:1px solid #dce1e8;position:absolute;vertical-align:middle;top:50%;right:0;margin-top:-0.15rem;transform:scaleX(.5);-webkit-transform:scaleX(.5)
}
.yun-divided.hidden-last-divided:last-child:after{display:none
}
.yun-border{border:1px solid #dce1e8
}
.yun-border-t{border-width:1px 0 0 0
}
.yun-border-b{border-width:0 0 1px 0
}
html[data-dpr="2"] .yun-border,html[data-dpr="3"] .yun-border{position:relative;border:none
}
html[data-dpr="2"] .yun-border:before,html[data-dpr="3"] .yun-border:before{z-index:1
}
html[data-dpr="2"] .yun-border:before{content:'';display:block;width:200%;height:200%;position:absolute;left:0;top:0;border:1px solid #dce1e8;transform-origin:left top;transform:scale(.5)
}
html[data-dpr="3"] .yun-border:before{content:'';display:block;width:300%;height:300%;position:absolute;left:0;top:0;border:1px solid #dce1e8;transform-origin:left top;transform:scale(.33333333)
}
html[data-dpr="2"] .yun-border-b:before,html[data-dpr="3"] .yun-border-b:before{border-width:0;height:1px;background:#dce1e8;top:initial;bottom:0
}
html[data-dpr="2"] .yun-border-t:before,html[data-dpr="3"] .yun-border-t:before{border-width:0;height:1px;background:#dce1e8;top:0;bottom:initial
}
.yun-flex-container{display:-ms-flexbox;display:flex
}
.yun-flex-item{-ms-flex:1;flex:1;display:block;width:0
}
.yun-flex-content{width:100%;overflow:hidden
}
.yun-grid-container{display:-ms-flexbox;display:flex
}
.yun-grid-col-1,.yun-grid-col-2,.yun-grid-col-3,.yun-grid-col-4,.yun-grid-col-5{display:block;width:0
}
.yun-grid-col-1{-ms-flex:1;flex:1
}
.yun-grid-col-2{-ms-flex:2;flex:2
}
.yun-grid-col-3{-ms-flex:3;flex:3
}
.yun-grid-col-4{-ms-flex:4;flex:4
}
.yun-grid-col-5{-ms-flex:5;flex:5
}
.yun-gird-content{width:100%;overflow:hidden
}
.yun-mask{position:fixed;width:100%;height:100%;top:0;left:0;z-index:2;background:rgba(0,0,0,0.4)
}
html,body{height:100%;min-height:100%
}
body{color:#030303;font-size:.26rem;font-family:-apple-system,Microsoft YaHei,Arial,Microsoft JhengHei,Helvetica Neue,sans-serif
}


